.home-page {
    .text-zone {
        position: absolute;
        left: 10%;
        top: 45%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 90%;
    }

    h1 {
        color: #fff;
        font-size: 53px;
        margin: 0;
        font-family: 'Coolvetica';
        font-weight: 400;
        line-height: 60px;
        white-space: nowrap;

        .name {
            font-size: 150px;
            line-height: 150px;
        }

        .letter {
            display: inline-block;

            &:hover {
                animation: jello 1s;
                color: #8BD8BD;
            }
        }

        .typed {
            color: #8BD8BD;
        }
    }

    h2 {
        color: #ddd;
        margin-top: 40px;
        font-weight: 400;
        font-size: 18px;
        animation: fadeIn 1s 1.8s backwards;
    }

    .flat-button {
        color: #8BD8BD;
        font-size: 18px;
        font-weight: 100;
        font-family: 'Coolvetica';
        letter-spacing: 4px;
        text-decoration: none;
        padding: 10px 18px;
        border: 1px solid #8BD8BD;
        margin-top: 15px;
        float: left;
        animation: fadeIn 1s 1.8s backwards;
        transition: all 0.3s ease-in;
        white-space: nowrap;

        &:hover {
            background: #8BD8BD;
            color: #333;
        }
    }

}