.headshot-container {
    z-index: 0;
    width: 505px;
    height: 630px;
    position: absolute;
    top: 12%;
    right: 14%;
    left: auto;
    margin: auto;
    white-space: nowrap;

    svg {
        width: 100%;
        height: auto;
    }

    .solid-headshot {
        position: absolute;
        top: 60px;
        right: auto;
        bottom: auto;
        left: 60px;
        margin: auto;
        width: 100%;
    }
}

.svg-container {
    stroke: #8BD8BD;
    stroke-width: 24px;
    overflow: visible;
}